// eslint-disable-next-line no-restricted-imports
import {observe} from '@github/selector-observer'
// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'
import {remoteForm} from '@github/remote-form'
import {trackView} from './hydro-tracking'

observe('.js-unwatch-suggestions[data-hydro-view]', trackingElement => {
  if (trackingElement instanceof HTMLElement) trackView(trackingElement)
})

function markAsRead(notification: Element): void {
  if (!notification.classList.contains('unread')) return
  notification.classList.remove('unread')
  notification.classList.add('read')
}

// Ensure notifications are marked as read after opening.
on('navigation:keyopen', '.js-notification', function (event) {
  markAsRead(event.currentTarget)
})

// Subscription landing page spinner
remoteForm('.js-notifications-subscription', async function (form, wants) {
  const spinner = form.querySelector<HTMLElement>('.js-spinner')!
  /* eslint-disable-next-line github/no-d-none */
  spinner.classList.remove('d-none')
  try {
    await wants.html()
  } catch (error) {
    // Ignore any error
  }
  /* eslint-disable-next-line github/no-d-none */
  spinner.classList.add('d-none')
})

on('change', '.js-notification-subscriptions-check', function () {
  const subscriptionChecked = document.querySelector('.js-notification-subscriptions-check:checked') != null
  document.querySelector<HTMLElement>('.js-notification-subscription-filters')!.hidden = subscriptionChecked
  document.querySelector<HTMLElement>('.js-notification-subscription-actions')!.hidden = !subscriptionChecked
})
